  opacity: 0.5;
  width: 10px;
  height: 10px;
  margin: 0 0.1em;
  padding: 0;
  border-radius: 50%;
  border: 2px solid transparent;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  line-height: 0;
  cursor: pointer;
}
